<template>
  <div id="app" v-on:click=close($event)>
    
    <!-- menu row -->
    <div id="menu">
      <h1 id="about" class="about menuItem" v-on:click=openMenu($event)> about Hilma </h1>
      <!--<h1 id="contact" class="contact menuItem" v-on:click=openMenu($event)> contact </h1>-->
    </div>

    <!-- project previews + components -->
    <div id="projectsContainer">

      <!-- For each project: previews -->
      <div class="projects" v-for="project in projects" :key="project.id" 
        :style="{'background-image': `url(${require(`./assets/${project.image}`)})`, 'background-repeat': 'no-repeat',
        'background-position': 'center',
        'background-size': 'cover'}"
        v-on:click=openProject($event)
        @mouseover="hover($event)"
        @mouseleave="un_hover($event)" 
      >

        <div id="banners">
          <h1 id="projName"> {{project.name}} </h1>  

          <!-- tool logos -->
          <ul id="logos"  class="banners">
            <li v-for="lang in project.languages" :key = "lang"
            :style=" { 'height' : 'max(2.5vw, 30px)', 
                      'width' : 'max(2.5vw, 30px)',
                      'background-image': `url(${require(`./assets/logos/${lang}.png`)})`,
                      'background-repeat': 'no-repeat',
                      'background-position': 'center',
                      'background-size': 'contain' }" > 
            </li>  
          </ul> 
        </div>

        <!-- pop-up component -->
        <Project  :projectName = "project.name"
                  :course = "project.course"
                  :courseLink = "project.courseLink"
                  :images = "project.more_images"
                  :description = "project.description" 
                  :repo = "project.repo"
                  :report = "project.report"
                  :languages = "project.languages"

                  :id="'project' + project.id"
                  :style="{'display': 'none'}"/>

      </div>  <!-- project -->
    </div>    <!-- projectsContainer -->

    <!-- menuTabs components -->
    <div id="menuTabs">
      <!--
      <MenuTab class="contact menuTab" name="contact" 
        :style="{'display': 'none'}"/>
      -->
      <MenuTab class="about menuTab" name="about" 
        :style="{'display': 'none'}"/>
    </div>

    <!-- error/help message -->
    <transition name="fade">
      <div v-if="error" id="errorMessage">
            <p> {{ errorMessage }} </p>
      </div>
    </transition>

  </div>      <!-- app -->

</template>

<script>
  // load json-data  
  import projectsData from '@/projects.json'
  import Project from '@/components/Project.vue'
  import MenuTab from '@/components/Menutab.vue'

  export default {
    name: 'App',
    components:{
      Project,
      MenuTab
    },
    data(){
      return {
        error: null,
        errorMessage: null,
        projects: projectsData // json-data
      }
    },
    created: function () {
      window.addEventListener('keyup', this.close_on_esc)
    },
    methods:{

      // on:click preview - show Project component
      openProject(event) { 

        let activeProject = this.get_active_project();
        let activeMenu = this.get_active_menu();
        
        // only open components if preview is at top of click
        // and if no component is already open
        var topElement = event.target;
        //console.log(topElement)
        if (topElement.className === "projects" || 
            topElement.id === "banners" ||
            topElement.parentElement.id === "banners" || 
            topElement.parentElement.id === "logos" ) {

          if (!activeProject && !activeMenu) {
              // get Project component of clicked preview
              let projectID = event.currentTarget.children[1].id;
              let clickedProject = document.getElementById(projectID);

              // show Project component
              unfade(clickedProject, true);
              //clickedProject.style = "display: flex; z-index: 5;";
              //console.log('clicked on preview of ', projectID)
          }
          // already active component  
          else {
            this.error = true;
            this.errorMessage = 'close active component b4 opening a new project pls :)';

            // show error message for 3 seconds
            setTimeout(() => { this.error = false; }, 3000)
          } 
        }
      },

      // on:click menuItem - show MenuTab component
      openMenu(event) {

        let activeProject = this.get_active_project();
        let activeMenu = this.get_active_menu();

        // only open components if no project is already open
        if (!activeProject && !activeMenu) {
          // find menu component
          let menuClass = event.currentTarget.classList[0];
          let menuTab = document.getElementsByClassName(menuClass)[1];
          unfade(menuTab);
          //menuTab.style = "display: flex;";
        }
        // already active component
        else {
          this.error = true;
          this.errorMessage = 'close active component b4 opening menu pls :)';

          // show error message for 3 seconds
          setTimeout(() => { this.error = false; }, 3000)
        } 
      },

      // get list of open project components
      get_active_project(){
        let activeProject = null;
        let projects = document.querySelectorAll('[id^="project"]');
        for (let i = 1; i < projects.length; i++){
            let style = projects[i].style.display;

            // save the showing Project component
            if (style !== 'none')
              activeProject = projects[i];
          }
          return activeProject;
      },

      // get list of open menu components
      get_active_menu(){
        let activeMenu = null;
        let menuTabs = document.getElementsByClassName('menuTab');
        for(let i = 0; i < menuTabs.length; i++){
          let style = menuTabs[i].style.display;
          
          // save the showing MenuTab component
          if (style !== 'none')
            activeMenu = menuTabs[i];
        }
        return activeMenu;
      },

      // on:click outside components 
      // - hide current showing Project/MenuTab component
      close(event) {
     
        // get element on top of click pos
        let topElement = event.target.id;

        // if click was outside any preview/project component
        if (topElement === 'app' ||
            topElement === 'projectsContainer' || 
            topElement === 'menu'){

          // hide open Project/MenuTab component
          this.hide_active_component();
        }
      },

      close_on_esc(event){
        /* function partly from @Tim Down: 
            - https://stackoverflow.com/a/3369743 */
        var isEscape = false;
      
        if ("key" in event) 
            isEscape = (event.key === "Escape" || event.key === "Esc");
        else 
          isEscape = (event.keyCode === 27);
        
        if (isEscape) {
          // hide open Project/MenuTab component
          this.hide_active_component();
        }
      },

      // hide active Project/MenuTab component
      hide_active_component(){

        // hide open Project component
        let activeProject = this.get_active_project();
        if (activeProject)
          activeProject.style.display = "none";
        
        // hide open MenuTab
        let activeMenu = this.get_active_menu();
        if (activeMenu)
          activeMenu.style.display = "none";
      },

      // @mouseover preview 
      hover(event){
        let preview = event.currentTarget;
        let projectName = preview.children[0].children[0];
        projectName.style.fontStyle = "italic";
        projectName.style.color = "#FFD6FF";
      },

      // @mouseleave preview
      un_hover(event){
        let preview = event.currentTarget;
        let projectName = preview.children[0].children[0];
        projectName.style.fontStyle = "normal";
        projectName.style.color = "black";
      }
    }
  }

  /* function from @Ibu: https://stackoverflow.com/a/6121270 */
  function unfade(element, project = false) {
    var op = 0.1;  // initial opacity
    element.style = 'display: flex;';
    if (project) element.style.zIndex = '5';
    var timer = setInterval(function () {
      if (op >= 1){
        clearInterval(timer);
      }
      element.style.opacity = op;
      element.style.filter = 'alpha(opacity=' + op * 100 + ")";
      op += op * 0.1;
    }, 15);
  }


</script>

