  background: -webkit-linear-gradient(left,  rgba(255,255,255,1) 0%,rgba(255,255,255,1) 59%,rgba(255,255,255,0.85) 65%,rgba(255,255,255,0) 100%); /* Chrome10-25,Safari5.1-6 */
